html {
  background-color: #171b24;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: normal;
  font-style: normal;
}

a {
  color: #69e27e;
}

.App nav {
  position: absolute;
  padding-top: 15px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.App nav img {
  width: 150px;
  margin-right: 30px;
  cursor: pointer;
}

#modal {
  font-weight: bolder;
  text-align: center;
}

#modal #close-icon {
  position: absolute;
  cursor: pointer;
  left: 280px;
  color: black;
  opacity: 0.5;
  margin-top: 5px;
}

#modal #qr-label {
  padding-top: 40px;
  font-family: "Source Sans Pro", sans-serif;
  color: #171b24;
  font-weight: lighter;
  margin-top: -21px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  text-align: center;
}

#modal img {
  width: 275px;
}

.landing-page-container {
  min-height: 600px;
  display: flex;
  background-image: url("./images/Background.png");
  height: 85vh;
  background-size: cover;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: normal;
  font-style: normal;
  flex-direction: row;
  margin: auto;
}

.landing-page-container > #details-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  flex: 0.5;
  padding: 15px;
}

.landing-page-container #details {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 40px;
  justify-content: center;
  flex-direction: column;
}

.landing-page-container #white {
  font-weight: 900;
  display: inline;
  margin: 0;
  color: #ffffff;
  letter-spacing: 1px;
  margin: 0;
  font-size: 25px;
  font-weight: 400;
}

#white::after {
  content: " ";
  white-space: pre;
}

.landing-page-container #description {
  letter-spacing: 1px;
  margin: 0;
  font-size: 25px;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.5);
}

.landing-page-container #download-options-container {
  justify-content: space-between;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 20px;
  margin-bottom: 30px;
  max-width: 350px;
}

.landing-page-container #screen-shot-container {
  background-size: contain;
  background-image: url("./images/screenshot.png");
  width: 60vw;
  height: 60vh;
  background-repeat: no-repeat;
  display: -ms-flexbox;
  background-position: calc(50%) 50%;
  display: flex;
}

.landing-page-container #screen-shot {
  justify-content: flex-end;
  align-items: center;
  display: flex;
  flex: 0.5;
}

.landing-page-container #screen-shot #image {
  height: 100%;
  width: 100%;
}

#download-button {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  color: white;
  background-color: #394152;
  height: 42px;
  width: 140px;
}

#download-options-container #option {
  padding: 10px;
}

#download-options-container img {
  width: 15px;
}

#download-options-container #display {
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
}
#download-options-container #size {
  color: #e6ecf2;
  margin-bottom: 15px;
  font-size: 11px;
}

#download-button:hover {
  transition: all 200ms ease-in;
  background-color: #5c677f;
}

#download-button img {
  color: white;
  margin-left: 15px;
}

#option > a {
  text-decoration: none;
}

footer {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 100;
  color: white;
  font-size: 14px;
  background-color: #171b24;
  border-top: solid #5c6a84;
}

footer #footer-content {
  padding: 10px;
  min-height: 100px;
  max-width: 1280px;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -ms-flex-align: center;
  align-items: center;
  width: 100%;
}

footer #donation {
  font-size: 17px;
  font-weight: 400;
  color: #ffffff;
}

footer #donation #modal-link {
  display: flex;
  align-items: center;
  height: 25px;
}

footer #donation i::after {
  content: " ";
  white-space: pre;
}

footer #donation #modal-link > a {
  text-decoration: none;
}

footer #donation #address {
  margin-top: 14px;
  color: rgba(255, 255, 255, 0.5);
  font-weight: 200;
  margin-top: 5px;
  margin-bottom: 10px;
  font-size: 13px;
}

footer #social {
  font-size: 17px;
  font-weight: 400;
  color: #ffffff;
}

footer #social #placeholder {
  margin-top: 15px;
  color: white;
  font-weight: 200;
  margin-top: 5px;
  margin-bottom: 10px;
}

footer #social #description {
  display: flex;
  flex-direction: row;
  margin-bottom: 5px;
}

footer #social-links {
  display: flex;
  flex-direction: row;
}

footer #social-links #link {
  display: flex;
  align-items: center;
  padding-right: 25px;
  text-decoration: none;
}

footer #social-links img {
  width: 25px;
  margin-left: 5px;
}

footer #social #description {
  font-weight: 400;
}

@media (max-width: 1440px) {
  .landing-page-container {
    flex-shrink: 0;
    flex-direction: row;
  }
  .landing-page-container #screen-shot-container {
    background-size: cover;
    width: 55vw;
    height: 75vh;
    background-position: calc(0%) 50%;
  }
}

@media (max-width: 1150px) {
  #download-button {
    width: 140px;
  }
  #download-button #size {
    font-size: 10px;
  }
  .landing-page-container #screen-shot-container {
    width: 50vw;
  }
}

@media (max-width: 1024px) {
  #details #neon-logo {
    padding-top: 20px;
  }
  .landing-page-container {
    height: auto;
  }
  .landing-page-container #screen-shot {
    background-size: contain;
    background-image: url("./images/screenshot.png");
    max-height: 280px;
    min-height: 280px;
    /*width: 100%;*/
    background-repeat: no-repeat;
    display: -ms-flexbox;
    background-position: calc(50%) 50%;
    display: flex;
    flex: 1;
    margin-bottom: 40px;
    margin-top: 20px;
  }
  .landing-page-container #screen-shot-container {
    display: none;
    min-width: 250px;
  }
  .landing-page-container #details-container {
    padding-bottom: 0;
  }
  .landing-page-container #details {
    padding-bottom: 0;
    padding-left: 0px;
    align-items: center;
    justify-content: space-evenly;
    text-align: center;
    /*margin-bottom: -25px;*/
  }

  .landing-page-container > #details-container {
    flex: 0.5;
  }
  .landing-page-container #description {
    font-size: 22px;
  }
  .landing-page-container #gold {
    font-size: 22px;
  }
  .landing-page-container h1 {
    font-size: 75px;
  }
  .landing-page-container {
    flex-shrink: 0;
    flex-direction: column;
  }
  .landing-page-container #download-options-container {
    margin-top: 30px;
    margin-bottom: 40px;
    flex-wrap: nowrap;
    max-width: 100%;
  }
  #download-options-container #option {
    padding: 10px;
  }
  footer #footer-content {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  footer #donation {
    padding: 20px;
    text-align: center;
  }
  footer #modal-link {
    justify-content: center;
    color: #69e27e;
  }
  footer #placeholder {
    display: none;
  }
  footer #social {
    padding: 20px;
  }
  footer #social #description {
    justify-content: center;
    margin-bottom: 5px;
  }
  footer #donation #address {
    justify-content: center;
    display: flex;
  }
}

@media (max-width: 768px) {
  #details #neon-logo {
    max-width: 200px;
  }
  .landing-page-container {
    height: auto;
  }
  .landing-page-container #details {
    justify-content: center;
  }
  .landing-page-container #download-options-container {
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 375px;
  }
  #download-options-container #option {
    padding: 5px;
  }
  .landing-page-container #screen-shot {
    min-height: 350px;
  }
  footer #address {
    font-size: 12px;
  }
}

@media (max-width: 450px) {
  .landing-page-container {
    text-align: center;
  }
  .landing-page-container #details {
    align-items: center;
    padding-left: 0px;
    justify-content: center;
  }
  .landing-page-container h1 {
    align-self: center;
    padding-top: 20px;
    font-size: 65px;
  }
  .landing-page-container #download-button {
    width: 125px;
  }
  .landing-page-container #download-options-container {
    justify-content: space-around;
    margin-top: 20px;
    flex-direction: row;
    flex-wrap: wrap;
  }
  #download-options-container #size {
    font-size: 10px;
  }
  .landing-page-container #description {
    font-size: 20px;
  }
  .landing-page-container #screen-shot {
    padding-bottom: 20px;
    min-height: 200px;
  }
  .App nav {
    justify-content: center;
  }
  footer #social-links #link {
    justify-content: space-between;
    max-width: 110px;
    min-width: 110px;
    padding: 10px;
  }
  footer #social-links {
    flex-direction: column;
    align-items: center;
  }
  footer #social-links img {
    margin-left: 10px;
  }
  footer #social {
    border-top: solid thin rgba(230, 236, 242, 0.2);
    justify-content: center;
    display: flex;
    flex-direction: column;
  }
  #modal img {
    width: 250px;
  }
  #modal #close-icon {
    left: 260px;
  }
}

@media (min-width: 1920px) {
  .landing-page-container {
    min-height: 1000px;
    flex-shrink: 0;
    flex-direction: column;
  }
  .landing-page-container #details {
    max-width: 750px;
    text-align: center;
    padding-left: 0;
  }
  .landing-page-container #details #neon-logo {
    align-self: center;
  }
  .landing-page-container #screen-shot {
    background-size: contain;
    background-image: url("./images/screenshot.png");
    background-repeat: no-repeat;
    display: -ms-flexbox;
    background-position: calc(50%) 50%;
    display: flex;
    flex: 1;
  }
  .landing-page-container #download-options-container {
    max-width: 100%;
  }
  .landing-page-container #screen-shot-container {
    display: none;
    min-width: 250px;
  }
}
